@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&family=Open+Sans:wght@400;700;800&display=swap);



/*font-family: 'Open Sans', sans-serif;*/
/*font-family: 'Open Sans Condensed', sans-serif;*/

* {
  margin: 0;
  padding: 0
}

a {
  transition: .5s;
  text-decoration: none
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: 'Open Sans', sans-serif;
}

button.btn, a.btn {
  font-size: 14px;
  padding: 8px 16px;
  color: #ffffff;
  background-color: #ff4530;
  border-radius: 5px;
  margin: 10px 0 10px;
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
}

.heading {
  text-align: center;
  position: relative;
  margin-bottom: 40px;
  padding-top: 16px;
}

.heading h2.title1 {
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 36px;
  text-transform: uppercase;
  line-height: 60px;
  margin: 0;
}

.heading h2.title {
  font-family: 'Open Sans', sans-serif;
  font-size: 32px;
  color: #aeaeae;
  font-weight: 700;
  margin: 0;
}

.heading h3.subtitle {
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 30px;
  text-transform: lowercase;
  color: #ce8242;
  margin: 0;
  line-height: 1
}

.heading p {
  color: #aeaeae !important;
  font-weight: 700;
  font-size: 13px;
  line-height: 2;
}

.heading:before {
  position: absolute;
  content: '';
  width: 60px;
  height: 4px;
  background: #c76f25;
  top: 0;
  left: calc(50% - 30px);
}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(255, 255, 255, 0.9)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e")
}

#header_area {
  background-color: rgba(0, 0, 0, 0.9)
}
#header_area .navbar-brand img{
  max-width: 200px;
}

#header_area ul#nav {
  text-align: center;
  margin: 0
}

#header_area ul#nav li {
  list-style: none;
  display: inline-block
}

#header_area ul#nav li a {
  color: #fff;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  display: block;
  padding: 30px 10px;
  position: relative;
}

#header_area ul#nav li a.active {
  position: relative
}

#header_area ul#nav li a.active::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 8px;

  content: '';
  background-color: #c76f25;
  transition: .5s
}

#header_area ul#nav li a::after {
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 8px;
  content: '';
  background-color: #c76f25;
  transition: .5s
}

#header_area ul#nav li a:hover::after {
  width: 100%
}

#header_area ul#nav li a:hover {
  color: #df4a43
}

#header_area.scrolled {
  background-color: rgba(28, 28, 28, 0.88);
  transition: padding 1s ease-in-out
}

#header_area.scrolled .navbar-brand img {
  /*    height: 70px*/
}

#header_area.scrolled ul#nav li a {
  padding: 15px 10px !important;
}

#banner-area {
  margin-top: 81px;
