  margin-top: 15px;
  font-size: 14px;
  font-weight: normal
}

footer .top-footer .footer-widgets .widget p {
  font-size: 15px;
  color: #5e5d5d;
  padding-bottom: 8px;
  margin: 0;
}

footer .top-footer .footer-widgets .widget a {
  font-size: 14px;
  text-decoration: none;
  color: #5e5d5d;
}

footer .top-footer .footer-widgets .widget a:hover {
  text-decoration: none;
}

footer .top-footer .footer-widgets .widget .useful-link {}

footer .top-footer .footer-widgets .widget .useful-link a {
  display: block;
  font-size: 17px;
  margin: .5rem 0;
  color: #5e5d5d;
}

footer .top-footer .footer-widgets .widget .useful-link a i {
  color: #2a2a2a;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: #b16e2e;
  text-align: center;
  line-height: 24px;
  margin-right: 5px;
}


footer .top-footer .footer-widgets .widget .contact-details .contact-info {
  display: flex;
  margin: .5rem 0;
}

footer .top-footer .footer-widgets .widget .contact-details .contact-info .icon {
  margin-right: 10px;
  color: #ffffff;
}

footer .top-footer .footer-widgets .widget .contact-details .contact-info p {
  color: #5e5d5d;
}





footer .bottom-footer .footer-copyright {
  padding-top: 20px;

}

footer .bottom-footer .footer-copyright p {
  color: #5e5d5d;
}

footer .bottom-footer .copyright {
  text-align: right;
  padding-top: 20px;
  color: #5e5d5d;

}

footer .bottom-footer .copyright a {
  font-size: 14px;
  text-decoration: none;
  color: #5e5d5d;
}

footer .bottom-footer .copyright a:hover {
  text-decoration: underline;
}

@media only screen and (min-width: 992px) and (max-width: 1200px) {

  #banner-area .banner-content img {
    bottom: -152%;
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  #banner-area .banner-content img {
    bottom: -182%;
  }
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
  #header_area {
    background-color: rgba(0, 0, 0, 0.9);
  }

  #header_area .container {
    width: 100%
  }

  #header_area .navbar-toggler {
    color: white;
    background-color: rgba(63, 63, 63, 0.9)
  }

  #header_area ul#nav {
    text-align: center;
    margin: 0
  }

  #header_area ul#nav li {
    list-style: none;
    display: inline-block
  }

  #header_area ul#nav li a {
    font-size: 16px;
    text-transform: uppercase;
    font-weight: bold;
    display: block;
    padding: 10px 10px
  }
  #header_area ul#nav li a.active::before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 2px;

    content: '';
    background-color: #c76f25;
    transition: .5s
  }
  #header_area ul#nav li a::after {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    content: '';
    background-color: #c76f25;
    transition: .5s
  }

  #header_area ul#nav li a:hover::after {
    width: 100%
  }

  #header_area ul#nav li a:hover {
    color: #c76f25
  }

  #banner-area .banner-content img {}
}

@media only screen and (max-width: 768px) {
  #banner-area {
    margin-top: 55px;
    background-color: rgba(0, 0, 0, 0.9) !important;
  }
  #footer_nav {
    display: none
  }

  #top_header_area {
    display: none
  }
}
@media only screen and (max-width: 576px) {
  footer .top-footer .footer-widgets{
    padding: 1rem ;
  }
  #component .component_details_content .component_details_child{
    padding: 1rem;
  }
  #business_details .business_details_content .business_details_child{
    padding: 1rem;
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .slider_main {
    width: 80% !important
  }

  .child_feature_area {
    width: 47% !important
  }
}

@media only screen and (min-width: 768px) {
  .footer {
    display: flex
  }

  .footer .footer-widgets {
    flex: 1 1
  }
}

@media only screen and (max-width: 1200px) {
  #banner-area {
  background-size: contain;


  }
}


